import React from "react";
import Checkbox from "./Checkbox";
import "./App.css";
import todoData from "../todoData";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      todos: todoData,
    };
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(id) {
    let tempArr = this.state.todos;
    tempArr[id].completed = !this.state.todos[id].completed;
    this.setState({ todos: tempArr });
  }

  render() {
    let components = this.state.todos.map(item => (
      <Checkbox
        key={item.id}
        item={item}
        // whatever={item.id}
        // text={item.text}
        // completed={item.completed}
        handleChange={this.handleChange}
      />
    ));
    return <div className='todo-list'>{components}</div>;
  }
}

export default App;
